    -webkit-transform: translateX(0);
            transform: translateX(0);
}
to {
    -webkit-transform: translateX(-50px);
            transform: translateX(-50px);
    opacity: 0;
}
}
@-webkit-keyframes coming {
from {
    -webkit-transform: translateX(-50px);
            transform: translateX(-50px);
    opacity: 0;
}
to {
    -webkit-transform: translateX(0px);
